<template>
    <div class="main">
        <div class="title">
            {{title}}
        </div>
        <div>
            <div class="bar">
                <div class="bar-last"></div>
                <div class="bar-active" :style="{ width : progress + '%'}"></div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "ProgressBar",
        props:{
            title: String,
            progress: Number,
        }
    }
</script>

<style lang="scss" scoped>
    .main{
        padding: 15px 0;
    }
    .bar{
        margin-top: 20px;
        width: 445px;
        height: 4px;
        position: relative;
        .bar-last{
            position: absolute;
            width: 445px;
            height: 4px;
            background: #C4C4C4;
        }
        .bar-active{
            position: absolute;
            height: 4px;
            background: #34547A;
        }
    }

</style>
